import { useState, useEffect, useContext } from 'react';
import CurrentUserContext from '../contexts/CurrentUserContext';
import Api from '../utils/api';

export default function useReadWatch() {
  const [guideData, setGuideData] = useState([]);
  const [videosData, setVideosData] = useState([]);
  const [articlesData, setArticlesData] = useState([]);
  const [moviesData, setMoviesData] = useState([]);
  const [booksData, setBooksData] = useState([]);
  const currentUser = useContext(CurrentUserContext);

  useEffect(() => {
    Promise.all([
      Api.getMaterials(),
      Api.getVideos(),
      Api.getArticles(),
      Api.getMovies(),
      Api.getBooks(),
    ])
      .then(([materials, videos, articles, movies, books]) => {
        setGuideData(materials);
        // –ù–µ–∞–≤—Ç–æ—Ä–∏–∑–æ–≤–∞–Ω–Ω—ã–π –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—å –Ω–µ –≤–∏–¥–∏—Ç –≤–∏–¥–µ–æ —Å —Ç–µ–≥–æ–º "–†–µ—Å—É—Ä—Å–Ω–∞—è –≥—Ä—É–ø–ø–∞"
        setVideosData(
          videos.filter(
            ({ tag }) => currentUser || !currentUser === (tag.name !== '–†–µ—Å—É—Ä—Å–Ω–∞—è –≥—Ä—É–ø–ø–∞')
          )
        );
        setArticlesData(articles);
        setMoviesData(movies);
        setBooksData(books);
      })
      .catch(console.log);
  }, []);

  return { guideData, videosData, articlesData, moviesData, booksData };
}
