import { useState, useEffect } from 'react';
import Api from '../utils/api';

export default function useBooksGenres(perPage) {
  const [booksData, setBooksData] = useState([]);
  const [genres, setGenres] = useState([]);
  const [pageCount, setPageCount] = useState(0);

  useEffect(() => {
    Api.getBooks()
      .then((data) => {
        const scienceBooks = data.filter((book) => book.genre === '–ù–∞—É—á–Ω—ã–µ');
        const fictionBooks = data.filter((book) => book.genre === '–•—É–¥–æ–∂–µ—Å—Ç–≤–µ–Ω–Ω—ã–µ');
        // –ø–æ —Ñ–∏–ª—å—Ç—Ä—É "–í—Å–µ" –∫–Ω–∏–≥–∏ –≤—ã—Ö–æ–¥—è—Ç –≤ –ø–æ—Ä—è–¥–∫–µ –Ω–∞—É—á–Ω–∞—è - —Ö—É–¥–æ–∂–µ—Å—Ç–≤–µ–Ω–Ω–∞—è - –Ω–∞—É—á - —Ö—É–¥ –∏ —Ç.–¥.
        const orderedBookList = scienceBooks
          .map((sb, i) => [sb, fictionBooks[i]])
          .flat()
          .filter((book) => book);
        setBooksData(orderedBookList);
        const genresData = data
          .map((item) => item.genre)
          .filter((item, i, arr) => arr.indexOf(item) === i);
        setGenres(['–í—Å–µ', ...genresData]);
        setPageCount(Math.ceil(data.length / perPage));
      })
      .catch(console.log);
  }, []);

  return { booksData, genres, pageCount };
}
