import { useForm } from 'react-hook-form';
import { PropTypes } from 'prop-types';
import Button from '../../UI/Button/Button';
import './QuestionsForm.css';

const QuestionsForm = ({ didAsk, onSubmit }) => {
  const {
    register,
    formState: { errors, isValid },
    handleSubmit,
  } = useForm({ mode: 'onChange' });

  return (
    <>
      <form
        className={`questions__form ${didAsk ? 'questions__form_hidden' : ''}`}
        name="ask"
        action="/"
        onSubmit={handleSubmit(onSubmit)}
      >
        <h3 className="questions__form-heading">
          –ï—Å–ª–∏ –≤—ã –Ω–µ –Ω–∞—à–ª–∏ –æ—Ç–≤–µ—Ç –Ω–∞ —Å–≤–æ–π –≤–æ–ø—Ä–æ—Å ‚Äî –Ω–∞–ø–∏—à–∏—Ç–µ –Ω–∞–º, –∏ –º—ã –≤–∫–ª—é—á–∏–º –µ–≥–æ –≤&nbsp;—Å–ø–∏—Å–æ–∫
        </h3>
        <div className="questions__ask">
          <input
            className={`questions__input ${errors.questionText ? 'questions__input_error' : ''}`}
            type="text"
            {...register('questionText', {
              required: '–ó–∞–¥–∞–π—Ç–µ —Å–≤–æ–π –≤–æ–ø—Ä–æ—Å',
              minLength: {
                value: 2,
                message: '–¢–µ–∫—Å—Ç –≤–æ–ø—Ä–æ—Å–∞ –¥–æ–ª–∂–µ–Ω –±—ã—Ç—å –Ω–µ –º–µ–Ω–µ–µ 2 —Å–∏–º–≤–æ–ª–æ–≤',
              },
            })}
            id="askme"
            placeholder={errors.questionText ? errors.questionText.message : '–í–≤–µ–¥–∏—Ç–µ –≤–æ–ø—Ä–æ—Å'}
          />
          <Button
            className="button button_color_darkgray popup__submit-btn"
            type="submit"
            disabled={!isValid}
          >
            –û—Ç–ø—Ä–∞–≤–∏—Ç—å
          </Button>
        </div>
      </form>

      <p className={`questions__result ${!didAsk ? 'questions__form_hidden' : ''}`}>
        –°–ø–∞—Å–∏–±–æ! –ú—ã –ø—Ä–∏–Ω—è–ª–∏ –≤–∞—à –≤–æ–ø—Ä–æ—Å.
      </p>
    </>
  );
};

QuestionsForm.propTypes = {
  didAsk: PropTypes.bool.isRequired,
  onSubmit: PropTypes.func.isRequired,
};

export default QuestionsForm;
